
button {
  background-color: cadetblue;
  border: none;
  color: white;
  padding: 6px 16px;
  border-radius: 2px;
  /*text-align: center;*/
  /*text-decoration: none;*/
  /*display: inline-block;*/
  font-size: 16px;
}
button.small {
  padding: 3px 8px;
  border-radius: 2px;
  font-size: 13px;
}
button:disabled {
  background: rgba(211, 211, 211, 0.5);
  color: rgba(0, 0, 0, 0.7);
}
.danger {
  background-color: #f00;
}
.unselectable {
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}
.ol-zoom {
  top: 100px;
  right: .5em;
  left: auto;
  left: initial;
}
.ol-dragzoom {
  border-color: #999;
  border-width: 2px;
  border-style: dashed;
}
.ol-mouse-position {
  position: absolute;
  color: #f3f3f3;
  -webkit-font-smoothing: antialiased;
  text-shadow: #000 0px 0px 2px, #000 0px 0px 2px, #000 0px 0px 2px;
  top: auto;
  bottom: 20px;
  right: 20px;
  font-size: 13px;
  font-weight: bold;
}



