
button {
  background-color: cadetblue;
  border: none;
  color: white;
  padding: 6px 16px;
  border-radius: 2px;
  /*text-align: center;*/
  /*text-decoration: none;*/
  /*display: inline-block;*/
  font-size: 16px;
}
button.small {
  padding: 3px 8px;
  border-radius: 2px;
  font-size: 13px;
}
button:disabled {
  background: rgba(211, 211, 211, 0.5);
  color: rgba(0, 0, 0, 0.7);
}
.danger {
  background-color: #f00;
}
.unselectable {
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  height: 100%;
}
html {
  height: 100%;
}
body {
  /* min-height: 100%; */
  height: 100%;
  overflow: hidden;
  background: url(assets/page-background.jpg) repeat 0 0;
}
.layers {
  position: absolute;
  top: 100px;
  left: 8px;
  overflow: hidden;
  bottom: 65px;
  pointer-events: none;
}
#ll_logos {
  position: absolute;
  bottom: 10px;
  left: 0px;
}
.iframe-modal-content {
  height: 100%;
}



